<template>
  <div id="profile-top">
    <div class="columns">
        <div class="column is-one-quarter">
          <slot />
        </div>
        <div class="column auto">
          <div class="card">
            <header :class="{'disabledDiv': user_Role != 'Admin'}" class="card-header">
              <a :class="{'disabledLink': user_Role != 'Admin'}" class="card-header-title" :href="link" target="_blank">
                {{title}}
              </a>
            </header>
            <div class="card-content">
              <div class="content">
                {{description}}
                <br>
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import userRolesMap from "utils/userRolesMap";

export default {
  props: ["title", "description", "link"],
  components: {},
  data() {
    return {
      user_Role: ""
    };
  },
  beforeMount() {
    this.user_Role = this.userRole();
  },
  methods: {
    userRole() {
      if (!this.merchant) {
        return "";
      }
      return userRolesMap[this.merchant.isAuthorized];
    }
  },
  computed: {
    ...mapGetters(["merchant"])
  }
};
</script>

<style>
.disabledLink {
  pointer-events: none;
  opacity: 0.6;
}
.disabledDiv {
  cursor: not-allowed;
}
</style>



